import React, { useContext } from "react";
import { PoolContext } from "../../Context/PoolContext";
import { useNavigate } from "react-router-dom";
import { TrashIcon } from "@heroicons/react/24/solid";
import axiosInstance from "Services/AxiosInstane";
import { Slide, toast } from "react-toastify";

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

const ShowClusters = () => {
  const navigate = useNavigate();
  const pc = useContext(PoolContext);
  const token = pc.token;

  let handleClusterSelection = (cluster) => {
    navigate(`/cluster/edit-cluster/${cluster.id}`);
  };

  let deleteCluster = (cluster_id) => {
    axiosInstance
      .delete(
        `${process.env.REACT_APP_BACKEND_URL}/v1/delete_cluster/${cluster_id}`,{
          headers: {
            Authorization: `Bearer ${token}`
          }
        }
      )
      .then((res) => {
        console.log(res.data)
        pc.setAvailableClusters([...res.data.clusters] || []);
        toast.success(res.data.msg, {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          transition: Slide,
        });
      })
      .catch((err) => {
        console.error("Error deleting cluster:", err);
        toast.error("Failed to delete cluster", {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          transition: Slide,
        });
      });
  };

  // Define columns with their alignment preferences
  const columns = [
    { header: "Name", key: "name", align: "left" },
    { header: "Type", key: "type", align: "left" },
    { header: "IP", key: "ip", align: "left" },
    { header: "Port", key: "port", align: "right" }
  ];

  return (
    <div className="show-pools flex justify-start items-start gap-2 h-auto w-[90%] min-h-[80vh] m-auto border-2 rounded-md shadow-inner">

      <div className="m-2 p-3 pb-0  w-full h-full overflow-y-auto overflow-x-hidden bg-white">
        <div className="flex justify-between mb-3 items-center">
          <h2 className="font-semibold leading-7 text-gray-700 text-lg ">
            Available Clusters
          </h2>
          <button
            onClick={() => navigate("/cluster/cluster-create-form")}
            className="bg-indigo-500 hover:bg-indigo-600 hover:text-gray-300 text-white rounded-md px-3 py-2 text-sm font-medium flex flex-col gap-2"
          >
            + New Cluster
          </button>
        </div>
        {pc.isClusterAvailable && (
          <div className="overflow-x-auto mt-10">
            <table className="w-full bg-white table-auto">
              <thead className="w-full">
                <tr className="rounded-md">
                  {columns.map((col) => (
                    <th
                      key={col.key}
                      className={`p-3 border-b-2 border-gray-400 text-sm uppercase font-bold text-${col.align}`}
                    >
                      {col.header}
                    </th>
                  ))}
                  <th className="p-3 border-b-2 border-gray-400 text-center text-sm uppercase font-bold">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody>
                {pc.availableClusters.map((item) => (
                  <tr
                    key={item.id}
                    className="border-b-2 hover:bg-gray-200 cursor-pointer"
                    onClick={() => handleClusterSelection(item)}
                  >
                    {columns.map((col) => (
                      <td
                        key={`${item.id}-${col.key}`}
                        className={`p-3 text-${col.align}`}
                      >
                        {item[col.key]}
                      </td>
                    ))}
                    <td className="p-3 text-center">
                      <TrashIcon
                        className="h-6 w-6 text-red-600 cursor-pointer inline-block"
                        onClick={(e) => {
                          e.stopPropagation();
                          deleteCluster(item.id);
                        }}
                      />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
};

export default ShowClusters;
